<template>
  <div>
    <ec-container height="100%">
      <ec-container-header>
        <ec-panel-item>
          <ec-panel-item-left>
            <ec-header title="进口烟到港码包分配领用" icon="onetomany" color-code="008" />
          </ec-panel-item-left>
        </ec-panel-item>
      </ec-container-header>

      <ec-container-body>
        <ec-panel>
          <ec-panel-toolbar style="overflow: hidden">
            <ec-panel-item>
              <a-descriptions bordered :column="4">
                <a-descriptions-item label="调拨年度">
                  {{ curYear }}
                </a-descriptions-item>
                <a-descriptions-item label="国别">
                  {{ getConturyName(conturys) }}</a-descriptions-item
                >
                <a-descriptions-item label="烟叶类型">
                  {{ getLeafTypeName(leafTypeCodes) }}
                </a-descriptions-item>
                <a-descriptions-item label="烟叶形态">
                  {{ getLeafFormName(leafFormCodes) }}
                </a-descriptions-item>

                <a-descriptions-item label="计划衔接量">
                  {{ top.planTotalWeight }}
                </a-descriptions-item>
                <a-descriptions-item label="申请量">
                  {{ top.collectNum }}
                </a-descriptions-item>
                <a-descriptions-item label="分配码包数量">
                  {{ top.allotNum }}
                </a-descriptions-item>
                <a-descriptions-item label="领用码包数量">
                  {{ top.collectNum }}
                </a-descriptions-item>
              </a-descriptions>
            </ec-panel-item>
            <ec-panel-item style="margin-top: 10px">
              <ec-panel-toolbar>
                <ec-query-panel
                  defaultFold
                  layout="horizontal"
                  :showFold="true"
                  @query="queryData"
                  @reset="onReset"
                >
                  <ec-query-item label="调拨年度">
                    <ec-select placeholder="请选择" v-model="curYear">
                      <a-select-option
                        v-for="item in prodYear"
                        :key="item.value"
                        :value="item.value"
                      >
                        {{ item.name }}
                      </a-select-option>
                    </ec-select>
                  </ec-query-item>
                  <ec-query-item label="供方单位">
                    <org-picker v-model="supplyOrgList" placeholder="请选择" @ok="getOrgName" />
                  </ec-query-item>
                  <ec-query-item label="供方仓库">
                    <ec-select showSearch allowClear placeholder="请选择" v-model="storeCode">
                      <a-select-option
                        v-for="(item, index) in cmTStore"
                        :key="index"
                        :value="item.storeCode"
                      >
                        {{ item.storeName }}
                      </a-select-option>
                    </ec-select>
                  </ec-query-item>
                  <template #inner>
                    <ec-query-item label="领用单位">
                      <org-picker v-model="supplyOrgLists" placeholder="请选择" />
                    </ec-query-item>
                    <ec-query-item label="烟叶类型">
                      <ec-select showSearch allowClear placeholder="请选择" v-model="leafTypeCodes">
                        <a-select-option
                          v-for="item in leafTypeCode"
                          :key="item.leafTypeName"
                          :value="item.leafTypeCode"
                        >
                          {{ item.leafTypeName }}
                        </a-select-option>
                      </ec-select>
                    </ec-query-item>
                    <ec-query-item label="烟叶形态">
                      <ec-select showSearch allowClear placeholder="请选择" v-model="leafFormCodes">
                        <a-select-option
                          v-for="item in leafFormCode"
                          :key="item.leafFormName"
                          :value="item.leafFormCode"
                        >
                          {{ item.leafFormName }}
                        </a-select-option>
                      </ec-select>
                    </ec-query-item>
                    <ec-query-item label="国别">
                      <ec-select showSearch allowClear placeholder="请选择" v-model="conturys">
                        <a-select-option
                          v-for="item in contury"
                          :key="item.leafCountryName"
                          :value="item.leafCountryCode"
                        >
                          {{ item.leafCountryName }}
                        </a-select-option>
                      </ec-select>
                    </ec-query-item>
                  </template>
                </ec-query-panel>
                <ec-divider />
              </ec-panel-toolbar>
            </ec-panel-item>

            <a-form :label-col="{ span: 22 }" :wrapper-col="{ span: 2 }">
              <a-form-item label="重量单位">
                <a-select placeholder="请选择公斤" v-model="unit">
                  <a-select-option value="公斤"> 公斤 </a-select-option>
                  <a-select-option value="担"> 担 </a-select-option>
                </a-select>
              </a-form-item>
            </a-form>
            <ec-panel-item>
              <ec-table
                show-footer
                border
                :data="tableData"
                :pagination="pagination"
                @page-action="pageAction"
                ref="table"
              >
                <ec-table-col type="seq" align="center" width="50" title="序号"></ec-table-col>
                <ec-table-col field="tranYear" align="center" title="调拨年度"></ec-table-col>
                <ec-table-col field="supplyOrgName" align="center" title="供方单位"></ec-table-col>
                <ec-table-col field="storeName" align="center" title="供方仓库"></ec-table-col>
                <ec-table-col field="demandOrgName" align="center" title="领用单位"></ec-table-col>
                <ec-table-col field="leafCountryName" align="center" title="国别"></ec-table-col>
                <ec-table-col field="leafTypeName" align="center" title="烟叶类型"></ec-table-col>
                <ec-table-col field="leafFormName" align="center" title="烟叶形态"></ec-table-col>
                <ec-table-col
                  field="planTotalWeight"
                  align="center"
                  title="计划衔接量"
                ></ec-table-col>
                <ec-table-col field="applyNum" align="center" title="申请码包数量"></ec-table-col>
                <ec-table-col field="applyNum" align="center" title="申请量"></ec-table-col>
                <ec-table-col field="allotNum" align="center" title="分配码包数量"></ec-table-col>
                <ec-table-col
                  field="applyWeight"
                  align="center"
                  title="领用码包数量"
                ></ec-table-col>
                <ec-table-col title="操作" align="right">
                  <template #default="{ row }">
                    <a-button type="link" @click="detail(row)">详细</a-button>
                  </template>
                </ec-table-col>
              </ec-table>
            </ec-panel-item>
          </ec-panel-toolbar>
        </ec-panel>
        <ec-drawer size="large" :visible="visibile" title="详细" @close="close">
          <ec-container height="auto" fixed="top bottom" color-code="007">
            <ec-container-body>
              <ec-panel auto>
                <ec-panel-body>
                  <div class="top">
                    <div class="left">
                      <div class="bg"></div>
                      <span>分配明细</span>
                    </div>
                    <div class="right">
                      <div class="year">调拨年度 : {{ curYear }}年</div>
                      <div>重量单位 :{{ unit }}</div>
                    </div>
                  </div>
                  <ec-table :data="details" border>
                    <ec-table-col type="seq" width="60" title="序号"></ec-table-col>
                    <ec-table-col field="tranYear" align="center" title="调拨年度"></ec-table-col>
                    <ec-table-col
                      field="leafCountryName"
                      align="center"
                      title="国别"
                    ></ec-table-col>
                    <ec-table-col
                      field="leafTypeName"
                      align="center"
                      title="烟叶类型"
                    ></ec-table-col>
                    <ec-table-col
                      field="leafFormName"
                      align="center"
                      title="烟叶形态"
                    ></ec-table-col>
                    <ec-table-col field="packType" align="center" title="包装类型"></ec-table-col>
                    <ec-table-col field="spec" align="center" title="规格(KG/件)"></ec-table-col>
                    <ec-table-col
                      field="allotNum"
                      align="center"
                      title="分配码包数量"
                    ></ec-table-col>
                    <ec-table-col field="applyWeight" align="center" title="领用码包数量">
                    </ec-table-col>
                  </ec-table>
                </ec-panel-body>
              </ec-panel>
            </ec-container-body>
          </ec-container>
        </ec-drawer>
      </ec-container-body>
    </ec-container>
  </div>
</template>

<script>
import OrgPicker from '@/components/TreePicker/src/OrgPicker.vue';
export default {
  components: { OrgPicker },
  data() {
    return {
      orgCode: '',
      details: [],
      storeCode: '',
      unit: '公斤',
      visibile: false,
      pagination: {
        pageSize: 10,
        pageNo: 1,
        total: 0,
      },
      supplyOrgList: [],
      supplyOrgLists: [],
      curYear: new Date().getFullYear(),
      // 国家
      contury: [],
      conturys: '',
      // 烟叶类型
      leafTypeCodes: '',
      leafTypeCode: [],
      // 烟叶形态
      leafFormCode: [],
      leafFormCodes: '',
      tableData: [],
      // 仓库
      cmTStore: [],
      top: {
        tranYear: '',
        leafTypeName: '',
        leafFormName: '',
        leafCountryName: '',
        planTotalWeight: '',
        leftWeight: '',
        collectNum: '',
        allotNum: '',
      },
    };
  },

  mounted() {
    this.getTableData();
    this.$apis.apm.admin.queryCountr({}).then((res) => {
      this.contury = res.rows;
    });
    this.$apis.apm.admin.queryType({}).then((res) => {
      this.leafTypeCode = res.rows;
    });
    this.$apis.apm.admin.queryForm({}).then((res) => {
      this.leafFormCode = res.rows;
    });
    // this.$apis.apm.admin.getCmTStoreByOrgCode({}).then((res) => {
    //   this.cmTStore = res.rows;
    // });
  },
  computed: {
    _api() {
      return this.$apis.packingSpec.apply;
    },
    prodYear() {
      const base = new Date().getFullYear();
      const specList = [];
      for (let i = 0; i < 10; i++) {
        specList.push({
          name: base - i,
          value: base - i,
        });
      }

      return specList;
    },
  },
  methods: {
    getLeafTypeName(code) {
      if (!code) return '';
      const levelList = this.leafTypeCode;
      const item = levelList.filter((levelItem) => levelItem.leafTypeCode === code);
      return item.length ? item[0].leafTypeName : '';
    },
    getLeafFormName(code) {
      if (!code) return '';
      const levelList = this.leafFormCode;
      const item = levelList.filter((levelItem) => levelItem.leafFormCode === code);
      return item.length ? item[0].leafFormName : '';
    },
    getConturyName(code) {
      if (!code) return '';
      const levelList = this.contury;

      const item = levelList.filter((levelItem) => levelItem.leafCountryCode === code);
      console.log(item);
      return item.length ? item[0].leafCountryName : '';
    },

    getOrgName(data) {
      const { selectOption } = data;
      this.orgCode = selectOption.length ? selectOption[0].orgCode : '';
      this.$apis.apm.admin.getCmTStoreByOrgCode({ orgCode: this.orgCode }).then((res) => {
        this.cmTStore = res.rows;
      });
    },
    onReset() {
      this.storeCode = '';
      this.applyDate = '';
      this.demandOrgCode = [];
      this.conturys = '';
      // 烟叶类型
      this.leafTypeCodes = '';
      this.supplyOrgLists = '';
      this.supplyOrgList = '';
      this.leafFormCodes = '';
      this.this.curYear = new Date().getFullYear();
    },
    close() {
      this.visibile = false;
    },
    onclose() {
      // this.$refs.modelRef.resetFields();
      this.tableData = [];
      this.visible = false;
    },
    detail() {
      // console.log(data);
      this.visibile = true;
      // 供方单位
      // const supplyOrgList = Array.isArray(this.supplyOrgList) ? supplyOrgList : [];
      // console.log(supplyOrgList);
      // //  领用单位
      // const contury = Array.isArray(this.contury) ? contury : [];

      this.$apis.apm.admin
        .detail({
          tranYear: this.tranYear,
          storeCode: this.storeCode,
          leafTypeCode: this.leafTypeCodes,
          leafFormCode: this.leafFormCodes,
          leafCountryCode: '106' || this.conturys,
          demandOrgCode: this.supplyOrgLists.join(''),
          procOrgCode: this.supplyOrgList.join(''),
          applyType: 'IMPORT_CP',
        })
        .then((res) => {
          this.details = res;
        });
    },
    getTableData() {
      const { leafTypeCodes, leafFormCodes, conturys, supplyOrgLists, curYear, applyDate } = this;
      const demandOrgCodes = supplyOrgLists.length ? supplyOrgLists.slice().join('') : '';
      const params = {
        applyType: 'IMPORT_CP',
        leafTypeCode: leafTypeCodes,
        leafFormCode: leafFormCodes,
        contury: conturys,
        applyDate,
        tranYear: curYear,
        demandOrgCode: demandOrgCodes,
      };
      this.$apis.apm.admin.query(params).then((res) => {
        // console.log(res);
        this.tableData = res.rows;
      });
    },
    pageAction(pageNo, pageSize) {
      this.pagination.pageSize = pageSize;
      this.pagination.pageNo = pageNo;
      const { leafTypeCodes, leafFormCodes, conturys, supplyOrgLists, curYear, applyDate } = this;
      const demandOrgCodes = supplyOrgLists.length ? supplyOrgLists.slice().join('') : '';
      const params = {
        applyType: 'IMPORT_CP',
        leafTypeCode: leafTypeCodes,
        leafFormCode: leafFormCodes,
        contury: conturys,
        applyDate,
        tranYear: curYear,
        demandOrgCode: demandOrgCodes,
        pageNo,
        pageSize,
      };
      this.$apis.apm.admin.query(params).then((res) => {
        // console.log(res);
        this.tableData = res.rows;
        this.pagination.total = res.totalRows || 0;
      });
      this.$apis.apm.admin.queryTop(params).then((res) => {
        this.top.tranYear = res.tranYear;
        this.top.leafTypeName = res.leafTypeName;
        this.top.leafFormName = res.leafFormName;
        this.top.leafCountryName = res.leafCountryName;
        this.top.planTotalWeight = res.planTotalWeight;
        this.top.leftWeight = res.leftWeight;
        this.top.collectNum = res.collectNum;
        this.top.allotNum = res.allotNum;
      });
    },
    queryData() {
      this.$refs.table.pageAction(this.pagination.pageNo, this.pagination.pageSize);
    },
  },
};
</script>

<style lang="less" scoped>
.table {
  display: flex;
  text-align: right;
  justify-content: space-around;
  height: 80px;
  table {
    width: 100%;
  }
  .first {
    background-color: #eee;
  }
}
.weight {
  display: flex;
  justify-content: flex-end;
}
.first {
  width: 50px;
}
.se {
  width: 50px;
}
.bg {
  height: 18px;
  width: 3px;
  background: blue;
  border-radius: 2px;
}
.top {
  display: flex;
  justify-content: space-between;
  .left {
    display: flex;
  }
  .right {
    width: 35%;
    display: flex;
    justify-content: space-between;
  }
  span {
    padding-left: 10px;
  }
}
</style>
